.about {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  padding-top: 200px;
  padding-bottom: 100px;
}

.aboutIntro {
  width: 80%;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  text-align: center;
  gap: 10px;
}

.aboutSectionContainer {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.aboutSection {
  width: 80%;
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

.aboutSectionTitle {
  flex: 50%;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.aboutSectionBody {
  flex: 50%;
  text-align: center;
}

.aboutSectionContainer p {
  color: black;
}

.aboutNumbersSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.historySection {
  width: 80%;
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}

.historyInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.historyInfo p {
  color: black;
}

.ourValuesSection {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 50px;
}

.ourValuesList {
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  gap: 35px;
}

.ourValuesList p {
  color: black;
}

.valueInfo {
  flex: 25%;
  display: flex;
  flex-direction: column;
}

.faqSection {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 70px;
}

.faqSectionHeader {
  width: 40%;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.faqContainer {
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
}
